.app-wrapper {
    margin: 0 auto;
    width: 1200px;
    display: grid;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    gap: 10px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: honeydew;
}

.preloader {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
}